
import {defineComponent} from "vue";
import {useRouter} from "vue-router";

export default defineComponent ({
  props: {
    user: {
      required: true
    }
  },
  setup(props, context) {
    const router = useRouter()
    const removeName = () => {
      context.emit('removeName')
      router.push({
        name:'login',
      })
    }
    return {
      removeName
    }
  },
})
